@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .3s;
    padding: 0;
    font-weight: inherit;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-big{
    font-size: 1.25em;
    padding: 0 1.2em;
    height: 3.102em;
    border-radius: .6em;
}

.m-btn-middle{
    font-size: .875em;
    padding: 0 1em;
    height: 2.86em;
    border-radius: .576em;
    svg{
        &:first-child{
            margin-right: .575em;
        }
    }
}

.m-btn-light{
    background-color: $light;
    color: $black;
    &:hover{
        background-color: #F4F0E9;
        color: rgba($black,.8);
    }
}

.m-btn-dark{
    background-color: $black;
    color: $lightGray;
    svg{
        fill:$lightGray;
    }
    &:hover{
        background-color: #60585A;
        color: $lightBg;
        svg{
            fill: $lightBg;
        }
    }
}

.text-btn{
    display: flex;
    align-items: center;
}

.text-btn-middle{
    font-size: 0.875em;
    line-height: 129%;
    color: $black;
    img{
        width: 1.575em;
        margin-right: .715em;
    }
    &:hover{
        color: $gray;
    }
}
